<template>
	<view class="" style="padding: 0 30rpx;">
		<view class="orderlist" v-for="(item,index) in orderlist" :key='index'>
			<view class="title">
				<text>{{item.paymentStatus}}</text>
				<text class="time_title">{{item.createTime|timeStamp}}</text>
			</view>
			<view class="count">
				<text>{{item.payAmount}}</text>
				<text class="unit"> 元</text>
			</view>
			<view class="item">
				<text class="time right" style="">订单状态</text>
				<text>{{item.orderStatus}}</text>
			</view>
			<view class="item">
				<text class="time right" style="">订单编号</text>
				<text>{{item.orderNo}}</text>
			</view>
			<view class="item">
				<text class="time right" style="">病历号</text>
				<text>{{item.caseHistoryNo}}</text>
			</view>
			<view class="item">
				<text class="time right" style="">影像员</text>
				<text>{{item.targetName}}</text>
			</view>
			<view class="detail" @click="enterDetail(item)">
				<text>查看详情</text>
				<uni-icons type="arrowright" color="#333333"></uni-icons>
			</view>
		</view>
		<u-loadmore :status="status"/>
	</view>
</template>

<script>
	import {getOrderList} from '@/utill/api/order/order.js'
	export default{
		data(){
			return{
				orderlist:[],
				status: 'nomore',
				page:1,
				total:0,
				mark:0,//第一次加载还是多次加载数据
			}
		},
		methods:{
			getOrderList(mark,page){
				var timer = setTimeout(function() {
					uni.showLoading({
					  title: '数据加载中...',
					})
				}, 2000)
				getOrderList({
					pageNum:page,
					pageSize:5
				}).then(res=>{
					clearTimeout(timer)
					uni.hideLoading()
					console.log(res)
					if(res.data.code==0){
						if(!res.data.object){//无数据
							this.status='nomore'
							return
						}
						this.total=res.data.object.pages
						if(mark==0){
							this.orderlist=res.data.object.list
						}else if(mark==1){
							this.orderlist=this.orderlist.concat(res.data.object.list)
						}
						this.orderlist.filter(item=>{
							if(item.paymentStatus=='901'){
								item.paymentStatus='待付款'
							}
							if(item.paymentStatus=='902'){
								item.paymentStatus='已付款'
							}
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
						return;
					}
				}).catch(err=>{
					clearTimeout(timer)
					uni.hideLoading()
					console.log(err)
				})
			},
			enterDetail(item){
				console.log(item)
				if(item.doctorConfirmStatus=='702'){
					uni.navigateTo({
						url:'/pages/orderManage/orderDetail?orderNo='+item.orderNo
					})
				}else{
					uni.showToast({
						title:'该订单未生成账单',
						icon:'none'
					})
				}
			},
			onReachBottom() {
				if(this.page >= this.total) return this.status = 'nomore';;
				this.status = 'loading';
				this.page++;
				console.log(this.page)
				this.mark=1
				this.getOrderList(this.mark,this.page)
			},	
		},
		onShow() {
			this.page=1
			this.total=0
			this.getOrderList(0,1)
		},
		onBackPress(e){
			console.log(e)
			var page =getCurrentPages()[0].route;
			uni.switchTab({
				url:'/'+page
			})
			return true
		}	
	}
</script>

<style scoped lang="scss">
	.orderlist{
		margin-top: 18rpx;
		background-color: #FFFFFF;
		border: 1px solid #F8F8F8;
		padding: 26rpx 30rpx 0;
		border-radius: 15rpx;
		.title{
			display: flex;
			justify-content: space-between;
			line-height: 56rpx;	
		}
		.time_title{
			color: gray;
		}
		.time{
			color: gray;
			width: 112rpx;
			display: block;
		}
		.right{
			margin-right: 30rpx;
		}
		.count{
			line-height: 80rpx;color: #000000;
			font-size: 60rpx;font-weight: bold;font-family: PingFang-SC-Bold;
			.unit{
				font-size: 30rpx;
				font-weight:100 ;
				font-family:PingFang-SC-Regular;
				color: #333333;
				margin-left: 10rpx;
			}
		}
		.item{
			line-height: 56rpx;
			display: flex;
		}
		.detail{
			margin-top: 40rpx;
			height: 100rpx;border-top: 1px solid #EEEEEE;
			display: flex;align-items: center;justify-content: space-between;
		}
	}
</style>
